@import "../../Common/commonStyle.less";
.footer-pc {
    padding-bottom: 10px;

    p,
    ul {
        margin: 0;
        padding: 0;
    }

    text-align: center;

    background:linear-gradient(270deg, rgba(100, 223, 222, 0.04) 0%, rgba(100, 223, 222, 0.1) 47%, rgba(100, 223, 222, 0.04) 100%);

    .footer-box {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        padding: 3% 12%;

        .footer-item {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;

            div {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;

                p {
                    font-family: @PingFang-SC-Medium-fontFamily;
                    font-weight: 500;
                    color: rgba(100, 223, 222, 1);
                }

                a {
                    font-family: @PingFangSC-Light-fontFamily;
                    font-weight: 300;
                    color: rgba(255, 255, 255, .7);
                }
            }
        }

        .footer-logo {
            cursor: pointer;
            flex-direction: row !important;
            align-items: center !important;



            div {
                text-align: left;
                margin-left: 10px;

                p {
                    font-family: @PingFang-SC-Medium-fontFamily;
                    font-weight: 500;
                    color: rgba(100, 223, 222, 1);
                }

                span {
                    font-family: @PingFangSC-Semibold-fontFamily;
                    font-weight: 600;
                    color: rgba(100, 223, 222, 1);
                }

            }
        }

        .footer-item div a:hover {
            text-shadow: 0px 2px 4px rgba(100, 223, 222, 1);
            color: rgba(255, 255, 255, 1);
        }
    }

    .explanationSegmentationImg {
        width: 100%;
        height: 12px;
    }

    .record {
        font-size: 14px;
        font-family: @PingFangSC-Light-fontFamily;
        font-weight: 300;
        color: rgba(255, 255, 255, .7);
        line-height: 20px;
        text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
    }
}

@media screen and(min-width: 1025px)and(max-width: 1440px) {
    .footer-pc {
        .footer-logo {
            img {
                width: 50px;
                height: 50px;
            }
            p {
                font-size: 28px;
                line-height: 40px;
            }

            span {
                font-size: 14px;
                line-height: 20px;
            }
        }

        .footer-title {
            font-size: 22px;
            line-height: 30px;
            margin-bottom: 5px !important;
        }

        a {

            font-size: 15px;
            line-height: 21px;
        }
    }
}

@media screen and(min-width: 1441px)and(max-width: 1920px) {
    .footer-pc {

        .footer-logo {

            img {
                width: 67px;
                height: 67px;
            }
            p {
                font-size: 37px;
                line-height: 53px;
            }

            span {
                font-size: 19px;
                line-height: 27px;
            }
        }

        .footer-title {
            font-size: 29px;
            line-height: 40px;
            margin-bottom: 5px !important;
        }

        a {
            font-size: 20px;
            line-height: 28px;
        }
    }
}